import React, { useState, useEffect, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Avatar from "@material-ui/core/Avatar";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Button from "@material-ui/core/Button";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { CopyToClipboard } from "react-copy-to-clipboard";
import FileCopyIcon from "@material-ui/icons/FileCopy";
import PublishIcon from '@material-ui/icons/Publish';
import Box from '@material-ui/core/Box';
import logo from "../logo/favicon.png";
import * as Services from "../../services/services";
import "./license.css";

const useStyles = makeStyles({
  root: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100%",
  },
  card: {
    width: "100%",
    maxWidth: "50rem",
    alignItems: "center",
  },
  cardcontent: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
  },
  bullet: {
    display: "inline-block",
    margin: "0 2px",
    transform: "scale(0.8)",
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
});

export default function License(props) {
  const classes = useStyles();
  const [license, setLicense] = useState("");
  let formLicense = useRef(null);

  const handleFileChange = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("licensefile", e.target.files[0]);
    Services.uploadLicenseFile(formData).then(async (response) => {
      if (response.data.errors) {
        props.showMessage(response.data.errors[0].msg, "error");
      } else {
        props.showMessage(
          "Licencia para " +
            response.data.company +
            " válida por: " +
            response.data.time +
            " días ",
          "success"
        );
        await Services.getConnectionsData();
      }
      if (formLicense.current) formLicense.current.reset();
    });
  };

  const handleCopied = (e) => {
    e.preventDefault();
    props.showMessage("Código copiado al portapapeles correctamente.", "success");
  };

  useEffect(() => {
    async function getEncryptSystemData() {
      await Services.getEncryptSystemData()
        .then((response) => {
          setLicense(response.data.license);
        })
        .catch((error) => {
          return;
        });
    }
    getEncryptSystemData();
  }, []);

  return (
    <div className={classes.root}>
      <form ref={formLicense}>
        <Card className={classes.card}>
          <CardContent className={classes.cardcontent}>
            <Avatar
              style={{ width: "70px", height: "70px", marginBottom: "15px" }}
              alt="logo"
              src={logo}
            />

            <Typography
              style={{
                minWidth: "100%",
                marginBottom: "15px",
                textAlign: "justify",
              }}
            >
              Lo sentimos necesita un archivo de licencia para usar el sistema,
              para obtener una licencia copie el siguiente código y envíelo a
              los proveedores del sistema.
            </Typography>

            <TextField
              style={{ minWidth: "100%", marginBottom: "15px" }}
              multiline
              rows={15}
              defaultValue={license}
              variant="outlined"
              inputProps={{ readonly: "readonly" }}
            />
            <Box display="flex" justifyContent="space-evenly" width="100%" flexWrap="wrap">
              <CopyToClipboard text={license}>
                <Button
                  color="primary"
                  variant="contained"
                  component="label"
                  onClick={(e) => {
                    handleCopied(e);
                  }}
                  style={{ margin: "0.2rem" }}
                >
                  <FileCopyIcon /> Copiar código  
                </Button>
              </CopyToClipboard>
              <Button color="secondary" variant="contained" component="label">
              <PublishIcon /> Cargar licencia
                <input
                  onChange={(e) => {
                    handleFileChange(e);
                  }}
                  type="file"
                  hidden
                />
              </Button>
            </Box>
          </CardContent>
        </Card>
      </form>
    </div>
  );
}
